import jss from "jss";
import preset from "jss-preset-default";
import once from "lodash";

const itisme =
  localStorage.ajdev === "ajdev" || location.href.includes("localhost");
console.info("aj loaded");

jss.setup(preset());

// Create your style.
const style = {
  myButton: {
    color: "green",
  },
  "@global": {
    "#bcpo": {
      marginLeft: "15px",
    },
    "select.single-option-selector": {
      borderColor: "black",
    },
  },
};

// Compile styles, apply plugins.
const sheet = jss.createStyleSheet(style);

// If you want to render on the client, insert it into DOM.
sheet.attach();

// If you want to render server-side, get the CSS text.
// sheet.toString();
// console.log(sheet.toString());

window.onload = () => {
  console.info("aj triggered");

  //1593792000000 is 2020-6-4
  //this removes liquid error text and powered by shopify text on page
  if (new Date().getTime() > 1593792000000 || itisme) {
    let selector = "//text()";
    var nodes = document.evaluate(
      selector,
      document.body,
      null,
      // XPathResult.ANY_TYPE,
      XPathResult.ORDERED_NODE_SNAPSHOT_TYPE,
      null
    );

    for (var i = 0; i < nodes.snapshotLength; i++) {
      console.log(nodes.snapshotItem(i).textContent);
      if (
        nodes.snapshotItem(i).textContent.includes("Liquid error:") ||
        /Powered\s+by\s+Shopify/.test(nodes.snapshotItem(i).textContent)
      ) {
        nodes.snapshotItem(i).textContent = "";
      }
    }
  }

  //1594310400000 is 2020-6-10
  //handle description tab content but with updated theme this is obsolete
  // if (new Date().getTime() > 1594310400000 || itisme) {
  //   const description =
  //     document.querySelector('div[itemprop="description"]') &&
  //     document.querySelector('div[itemprop="description"]').innerHTML;
  //   console.log(description);
  //   // $('div[itemprop="description"]').html();
  //   //some pages do not contain description
  //   if (description && description.trim()) {
  //     document.querySelector("#tab1>div").innerHTML = description;
  //     document.querySelector("#tab1>div").classList.add("clearfix");
  //   }
  // }
};

